import services from 'services'
const url = {
    getbanner : 'https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg',//banner
    
    gettoplist : 'http://c.y.qq.com/v8/fcg-bin/fcg_myqq_toplist.fcg', //

    getlistDetail : 'https://c.y.qq.com/v8/fcg-bin/fcg_v8_toplist_cp.fcg',//

    gethotkey : 'http://c.y.qq.com/splcloud/fcgi-bin/gethotkey.fcg',//

    search  : 'http://c.y.qq.com/splcloud/fcgi-bin/smartbox_new.fcg',//

    getlyc : 'https://api.darlin.me/music/lyric',//
    
    getsong : 'http://ws.stream.qqmusic.qq.com/' //
} 
class API {  //API
  constructor ({services, url}) {
    let serobj = {}
    this.url = url
    this.services = services
    Object.keys(url).forEach((item)=>{
       serobj[item] = services.jsonp.bind(null,url[item])
    })
    Object.assign(API.prototype,serobj) //
  }
  seturl (url, params) {
    return this.services.jsonp.call(this,url,params)
  }
}
export default new API({
  services,
  url
})



